body {
    margin: 0;
    padding: 0;
    color: #fff;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: black;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

h4 {
    font-style: italic;
    margin-top: -15px;
}

 
#outer-container {
    background-image: linear-gradient(rgba(0, 0, 0, 0.9) 35%, rgba(0, 0, 0, 0.65) 70%, rgba(0, 0, 0, 1) 100%);
    background-attachment: scroll;
    margin: 0;
    height: 120vh;
}


.App {
    -webkit-animation: fadein ease-out 1.4s;
            animation: fadein ease-out 1.4s;
}


.App-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 2%;
    padding-left: 5%;
}

.App-logo {
    width: 150px;
}


.logo-link:focus {
    outline: none;
}

.logo-link:focus,
.logo-link:hover>img {
    -webkit-filter: brightness(300%);
            filter: brightness(300%);
}

hr {
    border: 1px solid #01d277;
    width: 90%;
    position: absolute;
    margin-top: 75px;
    margin-bottom: 75px;
}

/* Search Form */
.search-form {
    position: relative;
}

input {
    background-color: transparent;
    color: #ffffff;
    font-size: 18px;
    padding: 10px 10px 10px 10px;
    width: 350px;
    border: none;
    border-bottom: 1px solid #01d277;
}

input:focus {
    outline: none;
}



/* Label */
label {
    color: #edfff7;
    font-style: italic;
    font-size: 18px;
    opacity: 0.5;
    position: absolute;
    top: 15px;
    left: 10px;
    transition: 0.2s ease all;
    pointer-events: none;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

/* Label Active State */
input:focus~label {
    top: -17px;
    left: 0px;
    font-style: normal;
    opacity: 1.0;
    font-size: 14px;
    color: #ffff;
}

/* Bottom Bar */
.search-bar {
    position: relative;
    display: block;
    width: 100%;
}

.search-bar:before,
.search-bar:after {
    content: '';
    height: 2px;
    width: 0;
    position: absolute;
    background: #01d277;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

.search-bar:before {
    left: 50%;
}

.search-bar:after {
    right: 50%;
}

/* Bottom Bar Active State */
input:focus~.search-bar:before,
input:focus~.search-bar:after {
    width: 50%;
}

/* Button */

.search-button {
    position: absolute;
    border: 2px solid #01d277;
    border-radius: 10px;
    font-size: 18px;
    padding: 0 5% 0 5%;
    left: 380px;
    top: 18px;
    background-color: transparent;
    color: #01d277;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

.search-button:focus {
    outline: none;
    color: #fff;
    border: 2px solid #ffff;
}

.search-button:after {
    content: '\279E';
}

.search-button:hover {
    background-color: #01d277;
    color: #081c24;
}

.search-button:active {
    background-color: #ffff;
    color: #081c24;
}


/* Media Queries */
@media only screen and (max-width: 650px) {
    .App-header {
        display: flex;
        flex-direction: column;
        margin-top: 20px;
    }

    .App-logo {
        width: 65vw;
        margin-left: 5%;
        margin-right: 10%;
    }

    .search-form {
        margin-right: 15%;
    }

    input {
        font-size: 18px;
        width: 200px;

    }

    .search-button {
        padding: 0 15px 0 15px;
        left: 225px;
        top: 16px;
    }

    hr {
        display: none;
    }

}

/* iPhone 4, 5, SE Landscape */
@media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) {
    .App-logo {
        width: 35vw;
        margin-left: 5%;
        margin-right: 10%;
    }

    input {
        width: 250px;

    }

    .search-button {

        left: 275px;

    }

}
/* Entire Movie Page */

.movie-container {
    padding: 0 5% 0 5%;
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

/* ============= Movie Poster & Details ============= */

/* container */
.movie {
    display: flex;
    flex-direction: row;
    height: 45vh;
    margin-top: 30px;
    margin-bottom: 25px;
}

.movie-page-poster {
    height: 100%;
    margin-right: 30px;
    border-radius: 10px;
    border: 1px solid #fff;
    background-color: #cbcbcb;
}

/* Overall Movie Data */
.movie-data {
    display: flex;
    flex-wrap: wrap;
}

.movie-headline {
    flex-basis: 100%;
    margin-bottom: -5vh;
}

/* Details & Plot */
.movie-details {
    flex-basis: 30%;
}

.movie-plot {
    flex-basis: 70%;
}

/* ============= Links ============= */
.genre-link>a {
    color: #01d277;
}

.genre-link>a:visited {
    color: #b2b2b2;
}

.genre-link>a:hover {
    text-decoration: none;
}

.genre-link>a:active {
    color: #fff;
}

.cast-container>a:hover,
.similar-movie-container>a:hover {
    color: #01d277;
}

.cast-container>a,
.similar-movie-container>a {
    text-decoration: none;
    color: #fff;
}


/* ============= Cast & Similar Movie Sliders ============= */


.cast-container,
.similar-movie-container {
    overflow: hidden;
    height: 40vh;
    padding-left: 10px;
    margin-right: -15px;
    margin-left: 15px;
}

.cast>.slider-list,
.similar-movies>.slider-list {
    width: 90%;
    margin: 0 auto;
}

.cast-container img:hover,
.similar-movie-container img:hover {
    -webkit-filter: brightness(110%);
            filter: brightness(110%);
}


.cast-photo,
.similar-movie-photo {
    height: 75%;
}

.cast-nophoto,
.similar-movie-nophoto {
    height: 75%;
    background-color: #cbcbcb;
}


.actor,
.similar-movie-title {
    font-weight: bold;
    margin-top: 0;
    margin-bottom: -5%;
}

.actor,
.character,
.similar-movie-title {
    width: 90%;
}

/* ============= Video Sliders ============= */
.video-container>iframe {
    width: 100vmin;
    height: 56vmin;
    position: relative;
    left: 5%;
}

.videos>.video-list {
    width: 75%;
    margin: 0 auto;
}

.video-list>button {
    top: 42%;
}

.video-list>.slick-next {
    right: 0vw;
}

.video-list>.slick-prev {
    left: -6vw;
}

/* ============= Slick Slider Styling ============= */
.slick-next,
.slick-prev {
    position: absolute;
    font-size: 0;
    top: 38%;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    cursor: pointer;
    border: none;
    background: 0 0;
    opacity: .75;
    color: #fff;
    transition: all .2s ease-in-out;
}

.slick-next:focus:before,
.slick-next:hover:before,
.slick-prev:focus:before,
.slick-prev:hover:before {
    opacity: 1;
    font-size: 40px;

}

.slick-next.slick-disabled:before,
.slick-prev.slick-disabled:before {
    opacity: .25
}

.slick-next:before,
.slick-prev:before {
    font-size: 30px;
    line-height: 1;
    opacity: .75;
    color: #fff
}

.slick-prev {
    left: -30px;
}

.slick-next {
    right: -35px
}

.slick-prev:before {
    content: '\AB'
}


.slick-next:before {
    content: '\BB'
}


.slick-list,
.slick-slider,
.slick-track {
    position: relative;
}

.slick-list {
    overflow: hidden;
}

.slick-list.dragging {
    cursor: pointer;
    cursor: hand
}

.slick-slider .slick-list,
.slick-slider .slick-track {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0)
}


.slick-slide {
    float: left;
}

.slick-slide.dragging img {
    pointer-events: none
}

@media only screen and (max-width: 768px) {
    .movie {
        flex-direction: column;
        height: auto;
        text-align: center;
    }


    .movie-details,
    .movie-plot,
    .movie-headline {
        margin: 0 auto;
        flex-basis: 100%;
    }

    .movie-page-poster {
        width: 100%;
        height: auto;
        margin-right: 0;
    }

    .cast,
    .similar-movies,
    .videos,
    .movie {
        width: 90vw;
        text-align: center;
    }

    .cast-container,
    .similar-movie-container {
        height: 65vh;
        margin: 0;
        padding: 0;
    }

    .actor,
    .character,
    .similar-movie-title {
        width: 90%;
        padding-left: 3vw;
    }

    .actor,
    .similar-movie-title {
        margin-bottom: -1%;
    }

    .video-container>iframe {
        width: 85vmin;
        height: 48vmin;
        left: 0;
    }

    .videos>.video-list {
        width: 90%;
        margin: 0 auto;
    }

    .video-list>.slick-next {
        right: -8vw;
    }

    .video-list>.slick-prev {
        left: -8vw;
    }

}
.results {
    padding-top: 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.movie-search-info>p {
    font-size: 1vmax;
}

.movie-search-info>h1 {
    font-size: 2vmax;
}

.results-num {
    font-size: 1vmax;
    padding-bottom: 15px;
}

.movie-card {
    height: 50vh;
    border: solid 1px #01d277;
    border-radius: 10px;
    width: 80%;
    margin-bottom: 20px;
    overflow: auto;
}

.movie-card:hover,
.movie-card:focus {
    opacity: 0.9;
    background-color: #01d277;
}

.results-poster,
.results-no-poster {
    height: 100%;
    float: left;
    margin-right: 2%;
    border-radius: 10px 0 0 10px;
    border-right: solid 1px #01d277;
    border-bottom: solid 1px #01d277;
}

.movie-search-info {
    padding-right: 2vw;
}

.results-no-poster {
    background-color: #cbcbcb;
}

.movie-card>a {
    text-decoration: none;
    color: #fff;
}

.movie-card:hover>a {
    color: #081c24;
}

/* Media Queries */

@media only screen and (max-width: 650px) {

    .movie-card {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        width: 95vw;
        height: 100vh;
        overflow: scroll;
    }

    .movie-search-info {
        padding: 0 3vw 0 3vw;
    }

}

@media only screen and (max-width: 768px) {

    .movie-card {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        width: 75vw;
        height: auto;
    }

    .movie-search-info>p {
        font-size: 2vmax;
    }

    .movie-search-info>h1 {
        font-size: 4vmax;
    }

    .results-num {
        font-size: 2vmax;
        padding-bottom: 15px;
    }


    .results-poster,
    .results-no-poster {
        float: none;
        width: 100%;
        height: auto;
        border-radius: 10px 10px 0 0;
        border-bottom: solid 1px #01d277;
    }

    .movie-search-info {
        padding: 0 3vw 0 3vw;
    }

}
